/******************************************
**
**  Reset CSS
**
******************************************/

html,body,div,span,applet,object,iframe,
h1,h2,h3,h4,h5,h6,p,blockquote,button,pre,
a,abbr,acronym,address,big,cite,code,
del,dfn,em,font,img,ins,kbd,q,s,samp,
small,strike,strong,sub,sup,tt,var,
dl,dt,dd,ol,ul,li,
fieldset,form,input,textarea,label,legend,
table,caption,tbody,tfoot,thead,tr,th,td{margin:0;padding:0;border:0;outline:0;font-weight:inherit;font-style:inherit;font-size:100.01%;vertical-align:baseline;}
:focus{outline:0}
body{line-height:1}
ol,ul{list-style:none}
/* tables still need 'cellspacing="0"' in the markup */
table{border-collapse:collapse;border-spacing:0;width:100%;}
caption,th,td{text-align:left;font-weight:normal}
blockquote:before,blockquote:after,
q:before,q:after{content:""}
blockquote,q{quotes:"" ""}
/* </reset from Eric Meyer, thanks! */


